<template>
	<div>
		<div v-for="i in count" v-bind:key='i.id'>
		    <p>{{i.name}}</p>
		    <ul>
		    	<li v-for="list in i.list" v-bind:key='list.id' v-if="list.tf">
		    		<a href="">{{list.name}}</a>
		    	</li>
		    </ul>
		</div>
	</div>
</template>
<style type="text/css">
  
</style>
<script>
export default {
  computed: {
    count () {
      return this.$store.state.navlist
    }
  }
}
</script>



